<template>
    <v-container :fluid="true">
      <div class="text-center">
          <h2>Fecha de Registro Patología</h2>
          <h3>Fecha:15/04/2021</h3>
      </div>
        <v-form @submit.prevent="Guardar"
        ref="formularioPatologia"
        lazy-validation
        >
      <v-row>
        <v-col  cols="12" sm="6" md="4" >
          <v-text-field :autofocus="true" :rules="dniRules"  v-model="dni" type="number" color="red darken-4"  label="DNI" dense outlined background-color="red lighten-4" ></v-text-field>
          <v-text-field  :rules="requeridoRules" v-model="nombres"  color="red darken-4" label="Nombres" dense outlined background-color="red lighten-4" ></v-text-field>
          <v-text-field :rules="requeridoRules" v-model="apellidos"  color="red darken-4" label="Apellidos" dense outlined background-color="red lighten-4" ></v-text-field>
       
        </v-col>
         <v-col cols="12" sm="6" md="4" >
          <!-- El item-text es el valor del nombre del objeto con el que se buscara en el array object  
          ES decir si pongo group o apellido buscara por grupo o apellido pero no recnocera nombre
           sino por el valor que ponga ahi
          -->
          <!-- item-value es el valor que ingresara al array del v-model si es que pongo que sea multiple -->
             <v-text-field :rules="requeridoRules"  v-model="fechaNacimiento" type="date" color="red darken-4" label="Fecha Nacimiento" dense  outlined background-color="red lighten-4"  ></v-text-field>
          <v-text-field :rules="telefonoRules" v-model="telefono" type="number"  color="red darken-4" label="Teléfono" dense  outlined background-color="red lighten-4"  ></v-text-field>
         <v-select v-model="empresa" :items="itemsEmpresa" item-text="descripcion" item-value="descripcion" item-color="red darken-4"  class="red--text text-lighten-4" label="Empresa" color="red darken-4" background-color="red lighten-4"  dense  outlined  ></v-select>
          <v-text-field :rules="empresaRules" v-if="empresa==='OTROS'" :autofocus="true"  v-model="nuevaEmpresa" type="text" color="red darken-4"  label="Nueva Empresa" dense  background-color="red lighten-4" ></v-text-field>         
        </v-col>
         <v-col cols="12" sm="6" md="4" >
          <v-select :items="dataTipoMuestraPat" 
          item-color="red darken-4" 
           class="red--text text-lighten-4" 
          label="Tipo de Muestra" 
          color="red darken-4" 
          background-color="red lighten-4"  
          item-text="descripcion"
          item-value="idTipoMuestraPat"
          v-model="dataTipoMuestraPatSelected"
          dense  outlined  ></v-select>
          
         <div class="d-flex flex-row "  v-if="dataMuestraPat.length>0" >
            <v-autocomplete
           
                  dense
                  v-model="examen"  
                  :items="dataMuestraPat"
                  outlined
                  chips
                  deletable-chips
                  color="red darken-4"
                  label="descripcion"
                  item-text="descripcion"
                  item-value="idMuestraPat"
                background-color="red lighten-4"            
                >
                  <!-- <template v-slot:item="data">
                      <v-list-item-content active-class="green--text" :disabled="true" >
                        <v-list-item-title @click="saludar()" class="red--text text-lighten-4" v-html="data.item.muestra"></v-list-item-title>
                      </v-list-item-content> 
                  </template> -->
                </v-autocomplete>
            <v-btn :disabled="examen===''?true:false" icon color="white" class="red darken-4 flex-row mx-2" @click="agregarExamen(examen)" ><v-icon>add</v-icon></v-btn>
        </div>         
        </v-col>
   </v-row>
   <h5>Examenes Realizados</h5>
   <p class="font-weight-bold red--text">{{mensajeExamen}} </p>
             {{examenesPat}}
             {{examenesRealizadosPat}}
            <v-row class="ma-0 pa-0" >              
              <v-col cols="12" sm="6" md="4 mb-2" class="ma-0 pa-0" v-for="(item,index) in examenesPat" :key ="index" >
                <v-card  outlined  class="pt-3 mx-1 px-1 red lighten-3" >
                  <v-btn @click="eliminarExamen(item.id)" class="float-right red" :x-small="true" color="white"  icon> <v-icon>close</v-icon></v-btn>
                  <v-row class="ma-0 pa-0">
                <v-col class="ma-0 pa-0" cols="6">
                <v-switch  dense  v-model="examenesRealizadosPat" :label="item.muestra" :value="item.id" class="ma-0 pa-0" color="red darken-4" >  </v-switch>
              </v-col>
              <v-col class="ma-0 pa-0" cols="5">
                 <v-text-field class="ma-0 pa-0" v-model="examenesPat[index].cantidad" type="number"  color="red darken-4" label="Cantidad" dense outlined background-color="red lighten-4"  ></v-text-field>
              </v-col>  
               </v-row>           
                  <v-row v-if="verificarExamenRealizado(item.id,index)" justify="center" class="ma-0 pa-0">
                    <v-col  cols="6" class="ma-0 pa-0">
                      <v-checkbox
                        class="ma-0 pa-0"  
                      dense
                      v-model="examenesPat[index].enviado"
                      label="¿Enviado?"
                      color="red darken-4"
                      :value="!enviado"
                      hide-details
                    ></v-checkbox>
                    <v-checkbox
                        class="ma-0 pa-0"  
                      dense
                      v-model="examenesPat[index].pagado"
                      label="¿Pagado?"
                      color="red darken-4"
                      :value="!pagado"
                      hide-details
                    ></v-checkbox>
                    </v-col>
                    <v-col  cols="6" class="ma-0 pa-0">
                          <v-text-field v-model="examenesPat[index].fechaResultado" type="date"  color="red darken-4" label="Entrega del Resultado" dense  outlined background-color="red lighten-4"  ></v-text-field>
                    </v-col>
                    </v-row>                   
                </v-card>
              </v-col>
            </v-row>
  <v-row justify="center" class="ma-0 pa-0">
    <v-col cols="6">
     <v-btn class="px-5" block dark color="red darken-4" type="submit">Guardar</v-btn>
    </v-col>
  </v-row>
  </v-form>
  <v-row>
     <v-col cols="11"  class="mx-auto">
      <Tabla @fillDataModificarPat="fillDataToMod"  ref="tabla" ></Tabla>
      </v-col>
  </v-row>
    </v-container>
</template>
<script>
import  {mapState} from 'vuex'
import Tabla from '../components/TablaPatologia'
export default {
    name:"LabClinico",
    components:{Tabla},
    data() {
        return {
           dniRules: [(v) => (!!v && (v.length==8) )|| "8 Numeros"],
          requeridoRules: [(v) => !!v || "es requerido"],
          empresaRules:[(v) => !!v || "es requerido"],
          telefonoRules:[(v) => (!!v && (v.length>8))|| "min 9 numeros"],
          examen:'',        
          //esta variable contiene a todos los que estana ctivos o examenes realizados switch True 
          examenesRealizadosPat:[],
          enviado:true,
          pagado:true,
          dataTipoMuestraPat:[],
          dataTipoMuestraPatSelected:0,
          dataMuestraPat:[],
          itemsEmpresa:[],
          //esta variable contiene TODO LOS DATOS de los examenes que seran registrados omodificados
        examenesPat:[],
        dni:'',
        nombres:'',
        apellidos:'',
        fechaNacimiento:'',
        telefono:'',
         empresa:'',
         nuevaEmpresa:''
      }
    },
    mounted(){
        this.getTipoMuestraPat();
      this.listarEmpresa();
    },
    computed:{
     
      mensajeExamen:function(){
        if(!this.examenesPat.length>0)
            return 'Se require seleccionar Examenes'
            return ''
        },
        ...mapState(['token'])
    },
    watch:{
          dataTipoMuestraPatSelected:function(val){
              if(val>0) this.getMuestraPat(val)
          }},
    methods: {
       listarEmpresa(){
        this.axios.get('/listarEmpresa')
        .then(res=>{
          console.log(res.data)
          res.data.push({idEmpresa:0,descripcion:'OTROS'});
          this.itemsEmpresa=res.data;
        })
        .catch(err=> console.log(err))
      },
        getTipoMuestraPat(){
          this.axios.get('/listarTipoMuestraPat')
          .then(res=>{this.dataTipoMuestraPat=res.data; })
          .catch(err=>{ console.log(err); })
        },
       getMuestraPat(e){
          this.axios.get(`/listarMuestraPat/${e}`)
          .then(res=>{  this.dataMuestraPat=res.data;       })
          .catch(err=>{   console.log(err);  })
          },
      FechaActual(){
          const hoy = new Date();
          var año=hoy.getFullYear()
          var mes=(hoy.getMonth() + 1)
              mes=(mes<10)?('0'+mes):mes;
          var dia=hoy.getDate()
              dia=(dia<10)?('0'+dia):dia;
       return año+'-'+mes+'-'+dia;
      },
      convertidorFecha(str){
          if(str==null){
            return 'Sin Data'
          }else{
            const f = new Date(str);
              var año=f.getFullYear()
              var mes=(f.getMonth() + 1)
                  mes=(mes<10)?('0'+mes):mes;
              var dia=f.getDate()
                  dia=(dia<10)?('0'+dia):dia;
          return año+'-'+mes+'-'+dia;
          }
     },
      Guardar(){
         var headers = {
        "Content-Type": "application/json",
        token: this.token,
       };
        var existeExamen=this.examenesPat.length>0;
        var regPatologia = this.$refs.formularioPatologia.validate();

        if(existeExamen && regPatologia ){
          this.axios.post("/saveExamPatologia",{
                  dni:this.dni,
                  nombres:this.nombres.trim(),
                  apellidos:this.apellidos.trim(),
                  fechaNacimiento:this.fechaNacimiento,
                  telefono:this.telefono,
                  empresa:this.nuevaEmpresa===''? this.empresa:this.nuevaEmpresa,
                  fechaRegistroExamen:this.FechaActual(),
                  examenes:this.examenesPat
          },{headers}) 
          .then(res=>{
            console.log(res.data)
                this.dni=''
                this.nombres=''
                this.apellidos=''
                this.fechaNacimiento=''
                this.telefono=''
                this.empresa=''
                this.examenesPat=[]
                this.examenesRealizadosPat=[]
          })
          .catch(err=>{console.log(err)})  
         
        }
        
      // this.axios.post(url,{},{headers}){
      // }
      this.$refs.tabla.ListarMuestras();
      },
      verificarExamenRealizado(ele,i){
        
            var existe=this.examenesRealizadosPat.includes(ele);
            if(!existe){
                   this.examenesPat[i].atendido=false
                   this.examenesPat[i].pagado=false
                   this.examenesPat[i].enviado=false
                   this.examenesPat[i].fechaResultado=''}
                  else{
                      this.examenesPat[i].atendido=true
                   }

      return existe;
      },
      agregarExamen(e){
        if(e!==null){
          //este dato sera agregado a la lista de los card examenes
            var examsPat={};
              this.dataMuestraPat.filter(ele=>{if(ele.idMuestraPat===e) examsPat={id:ele.idMuestraPat,muestra:ele.descripcion,atendido:false,cantidad:1,pagado:false,enviado:false,fechaResultado:''} });
              var existe=false;
            this.examenesPat.filter(x=>{ if(x.id===examsPat.id) existe=true })  
            
                  if(!existe)
                    this.examenesPat.push(examsPat);
                  this.examen='' 
        }
            

      },
      agregarExamenParaModificar(e){
        if(e!==null){
       
            var examsPat={};
              this.dataMuestraPat.filter(ele=>{if(ele.idMuestraPat===e) examsPat={id:ele.idMuestraPat,muestra:ele.descripcion,atendido:false,cantidad:1,pagado:false,enviado:false,fechaResultado:''} });
              var existe=false;
            this.examenesPat.filter(x=>{ if(x.id===examsPat.id) existe=true })  
            
                  if(!existe)
                    this.examenesPat.push(examsPat);
                  this.examen='' 
        }
            

      },
      eliminarExamen(e){  
          this.examenesPat= this.examenesPat.filter(elem=>{ if(elem.id!==e) {return elem}})
          this.examenesRealizadosPat=this.examenesRealizadosPat.filter(ele=>{if(ele!==e) {return ele}})
          
      },
      fillDataToMod(d){
console.log('Padre aptologia')
console.log(d)
//Vaciamos par modificar otro dato
          this.examenesPat=[];
          this.examenesRealizadosPat=[];
              this.dni=d.dni
                this.nombres=d.nombres
                this.apellidos=d.apellidos
                this.fechaNacimiento=this.convertidorFecha(d.fechNac)
                this.telefono=d.telefono
                this.empresa=d.empresa
                // this.dataTipoMuestraPatSelected=d.idTipoMuestraPat
                // this.examen=d.idMuestraPat
                /*
                apellidos: "Tomate Reynaldo"
cantidad: 1
dni: "76354821"
empresa: "Pams"
estadoPago: 0
examen: "Losenge"
fechaAtencion: null
fechaEntregaResultado: null
fechaEnvioDocExamen: "2021-04-24T05:00:00.000Z"
fechaEnvioMuestra: "2021-04-24T05:00:00.000Z"
fechaNacimiento: "1996-04-19T05:00:00.000Z"
fechaRegistroExamen: "2021-04-24T05:00:00.000Z"
idMuestraPat: 2
idPaciente: 7
idTipoMuestraPat: 1
nombres: "Belinda"
telefono: "123123145"
                */ 
               console.log( d.detalleExamen.length)
               console.log( d.detalleExamen)
                var iterador=0;
               for (let index = 0; index < d.detalleExamen.length; index++) {
                 
                   console.log(d.detalleExamen[index].fechaAtencion)
                   console.log()
                   if (!(d.detalleExamen[index].fechaAtencion==null)){
                   
                     //agregando a los examenes realizados si es que tien fecha de atendido
                      this.examenesRealizadosPat[iterador]=d.detalleExamen[index].idMuestraPat
                      iterador++;
                  }  
                  this.examenesPat.push({
                  id:d.detalleExamen[index].idMuestraPat,
                  muestra:d.detalleExamen[index].examen,
                  atendido:d.detalleExamen[index].fechaAtencion==null? false:true ,cantidad:1,
                  pagado:d.detalleExamen[index].estadoPago==0? false:true,
                  enviado:d.detalleExamen[index].fechaEnvioMuestra==null? false:true ,
                  fechaResultado:d.detalleExamen[index].fechaEntregaResultado==null? '':this.convertidorFecha(d.detalleExamen[index].fechaEntregaResultado)})
               } 
               iterador=0;
      }
    },
}
</script>
<style scoped>
.v-input__slot{
margin:0;
}
.v-input__control.v-messages.theme--light.red--text .text--darken-4{
  display: none !important;
}
.theme--light.v-application{
  background-color:#FFCDD2;
}

.theme--light.v-list{
  background: #FFCDD2;
}

::v-deep input::-webkit-outer-spin-button,
::v-deep input::-webkit-inner-spin-button {
-webkit-appearance: none;
margin: 0;
}

</style>